.copyright {
  display: flex;
  padding: 14px 0;
  padding-right: 12.6%;
  padding-left: 13.3%;
}

.copyright__text {
  @include font(13px, 24px);
  margin: 0;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  opacity: 0.5;
  margin-right: 10.2%;

  &:first-child {
    margin-right: 4.9%;
  }

  &:last-child {
    margin-left: auto;
    margin-right: 0;
  }
}

.copyright__link {
  color: $dark-color;

  &:hover {
    text-decoration: underline;
    opacity: 1;
  }

  &:focus {
    opacity: 0.8;
  }
}

@media (max-width: $tablet-width) {
  .copyright {
    justify-content: space-between;
    padding-top: 21px;
    padding-right: 1.5%;
    padding-bottom: 24px;
    padding-left: 3.4%;
  }

  .copyright__text--extra {
    display: none;
  }
}

@media (max-width: $mobile-width) {
  .copyright {
    flex-direction: column;
    align-items: center;
    padding: 26px 0;
    padding-left: 4%;
  }

  .copyright__text {
    margin: 0;
    margin-bottom: 19px;

    &:first-child,
    &:last-child {
      margin: 0;
    }
  }
}
